class FirebaseJsonStore implements CrosswordPuzzleJsonStore {
    getDetailsAsync(): Promise<CrosswordPuzzleChooseDetail[]> {
        var self = this;
        return new Promise<CrosswordPuzzleChooseDetail[]>(function (resolve, reject) {
            var lookups = firebase.database().ref("crosswordLookups");
            //https://firebase.google.com/docs/reference/js/firebase.database.DataSnapshot
            lookups.once('value', function (snapshot) {
                var details: CrosswordPuzzleChooseDetail[] = [];
                snapshot.forEach(function (childSnapshot) {
                    var childKey = childSnapshot.key;
                    var childData = childSnapshot.val();
                    details.push(new FirebaseJsonChooseDetail(self, childData.title, childData.id, childData.datePublished as string));
                    return false
                });
                resolve(details);

            }, function () {
                //do something 
                console.log("Error getting details")
                reject();
            });
        })
    }
    getCrosswordAsync(id: string): Promise<CrosswordModelJson> {
        return new Promise < CrosswordModelJson>(function (resolve, reject) {
            firebase.database().ref("crosswords/" + id).once("value", function (snapshot) {
                var crossword = snapshot.val() as CrosswordModelJson;
                resolve(crossword);
            }, function () {
                //do something
                console.log("Error getting crossword ");
                reject();
            })
        });
        
    }

}
class FirebaseJsonChooseDetail implements CrosswordPuzzleChooseDetail {
    
    store: FirebaseJsonStore
    constructor( store: FirebaseJsonStore, title: string, id: string,datePublished:string) {
        this.inStore = false;
        this.title = title;
        this.id = id;
        this.datePublished = new Date(datePublished);
        this.store = store;
        
    }
    inStore: boolean;
    getAsync(): Promise<CrosswordModelJson> {
        return this.store.getCrosswordAsync(this.id);
    }
    title: string;
    id: string;
    datePublished: Date;


}
export var crosswordPuzzleJsonStore: CrosswordPuzzleJsonStore = new FirebaseJsonStore();
